@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,400;1,500;1,600&family=Raleway:ital,wght@0,300;0,400;0,500;0,600;0,700;1,400;1,500;1,600&display=swap);
@tailwind base;
@tailwind components;
@tailwind utilities;
/* css reset */
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  background: transparent;
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #aaa;
  border-radius: 4px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #ddd;
  border-radius: 4px;
  width: 3px;
  margin: 1px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #fff;
}

h1,
h2,
h3,
h4,
input,
textarea {
  font-family: "Montserrat", sans-serif;
}

p {
  font-family: "Raleway", sans-serif;
  letter-spacing: 0.06em;
}

a,
button {
  font-family: "Raleway", sans-serif;
  text-decoration: none;
  font-weight: 500;
  transition: all 0.5s;
}

body {
  width: 100vw;
}

h1 {
  font-size: 80px;
  font-weight: 700;
  color: #000;
  margin: 10px 0;
  padding: 0;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  text-align: center;
}

h2 {
  font-size: 40px;
  font-weight: 600;
  color: #000;
  margin: 0;
  margin-bottom: 10px;
  padding: 0;
  text-transform: capitalize;
  text-align: center;
}

h3 {
  font-size: 24px;
  font-weight: 600;
  color: #000;
  margin: 0;
  padding: 0;
  text-transform: capitalize;
  text-align: center;
}

p {
  font-size: 20px;
  font-weight: 400;
  color: #000;
  margin: 0;
  padding: 0;
  text-align: center;
}

hr {
  width: 100%;
  height: 2px;
  background: #6664;
  margin-bottom: 20px;
}
ul {
  list-style-type: none;
}

a {
  color: #000;
  transition: 0.5s all ease;
}
a:hover {
  text-decoration: none;
  opacity: 0.8;
}
.link {
  text-decoration: none;
  color: #000;
  font-size: 20px;
  letter-spacing: 0.05em;
  font-weight: 600;
  padding: 0;
  margin: 0;
  transition: 0.4s all ease;
}

.link:hover {
