    136.22deg,
    rgba(128, 196, 205, 0.68) 8.63%,
    rgba(112, 129, 214, 0.477341) 29.43%,
    rgba(122, 220, 220, 0.53) 50.77%,
    rgba(226, 158, 250, 0.497914) 69.63%,
    rgba(252, 157, 214, 0.47) 78.62%
  );
  padding-top: 40px;
}

#hero {
  width: 100vw;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 0 10vw;
}

.header-image {
  width: 200px;
  height: 200px;
}

.socials {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 20px;
  width: 300px;
}

.socials > li > a {
  color: #000;
  font-size: 30px;
  text-decoration: none;
  transition: all 0.3s;
  cursor: pointer;
}
.socials > li > a:hover {
