  }

  #about {
    padding-left: 8vw;
    padding-right: 8vw;
  }

  #about picture {
    width: 100%;
  }

  .about-description {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0;
  }

  .about-description p,
  .music-header p {
    text-align: center;
  }

  #music {
    padding-left: 8vw;
    padding-right: 8vw;
    width: 100vw;
    flex-direction: column;
    justify-content: flex-start;
  }

  .music-header {
    margin-bottom: 40px;
    height: auto;
  }

  .audio-header {
    margin-bottom: 30px !important;
    margin-top: 50px !important;
    height: 20px;
    font-size: 26 !important;
  }

  #contact {
    padding-left: 7vw;
    padding-right: 7vw;
    width: 100vw;
  }

  .form-description {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    padding-bottom: 0;
  }

  .form-description p {
    text-align: center;
    padding-right: 0;
  }
  .contact-info {
    height: auto;
  }
  .socials {
    margin-bottom: 50px;
    justify-content: space-evenly !important;
    padding: 20px 15% 0;
  }

  .form-container {
    width: 100%;
  }
  .title {
    font-size: 23 !important;
  }
  .album {
    font-size: 20 !important;
  }
}

