#nav {
  width: 100vw;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 20%;
  padding-right: 20%;
  position: fixed;
  top: 20px;
  z-index: 2;
}

#nav ul {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  border-radius: 30px;
  height: 60px;
  transition: 0.5s all ease;
  -ms-transition: 0.5s all ease;
  -webkit-transition: 0.5s all ease;
  margin: 0;
}

.navigation {
  position: absolute;
  height: 60px;
  border-radius: 30px;
  z-index: -1;
  transition: 0.5s all ease;
  -ms-transition: 0.5s all ease;
  -webkit-transition: 0.5s all ease;
}

.nav-inactive {
  width: 60vw;
  background-color: linear-gradient(93.39deg, #c2ddf1 11.95%, #e7c6e5 100.67%);
  opacity: 0;
}

.nav-active {
  width: 55vw;
  background: linear-gradient(93.39deg, #c2ddf1 11.95%, #e7c6e5 100.67%);
  border-radius: 30px;
  box-shadow: 0px 0px 10px #b0b0b088;
  padding-left: 20px;
  padding-right: 20px;
}

#nav a {
  text-decoration: none;
  color: #000;
  font-size: 20px;
  letter-spacing: 0.05em;
  font-weight: 600;
  padding: 0;
  margin: 0;
}

#nav li {
  transition: 0.4s all;
  opacity: 1 !important;
}

#nav li:hover {
  -webkit-transform: translateY(3px) scale3d(1.03, 1.03, 1.01);
          transform: translateY(3px) scale3d(1.03, 1.03, 1.01);
}

