.project-description p {
  text-align: left;
  font-size: 20px;
}

.project picture {
  width: 40%;
  padding-left: 5%;
  padding-right: 5%;
}

.project-name {
  position: relative;
  padding-top: 2px;
  padding-bottom: 2px;
}

.project-name::before {
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transform-origin: bottom right;
          transform-origin: bottom right;
}

.project-name:hover::before {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
}

.project-name::before {
  content: " ";
  display: inline;
  position: absolute;
  inset: 0 0 0 0;
  background: var(--blue-med);
  z-index: -1;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.link-underline {
  display: inline-block;
  position: relative;
  color: var(--blue-med);
}

.link-underline:after {
  content: "";
  position: absolute;
  width: 100%;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: var(--blue-med);
  -webkit-transform-origin: bottom right;
          transform-origin: bottom right;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
}

.link-underline:hover:after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
}

.project-image {
  object-fit: contain;
  height: auto;
  width: auto;
  max-height: 100%;
  max-width: 100%;
  margin-top: 20px;
  margin-bottom: 20px;
  transition: all 0.5s;
}

.project-image:hover {
  -webkit-filter: drop-shadow(0px 0px 10px var(--blue-light));
          filter: drop-shadow(0px 0px 10px var(--blue-light));
  -webkit-transform: scale3d(1.01, 1.01, 1.01);
  transform: scale3d(1.01, 1.01, 1.01);
}

.project-tags-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin-top: 5px;
  margin-bottom: 50px;
}

.project-tags {
  border-radius: 18px;
  padding: 3px 15px;
  margin: 10px 10px 10px 0px;
  max-width: 150px;
  text-align: center;
  transition: all 0.3s;
}

.project-tags:hover {
  -webkit-transform: scale3d(1.1, 1.1, 1.01);
  transform: scale3d(1.1, 1.1, 1.01);
}

.project-tags p {
  font-size: 14px !important;
  font-weight: 600;
  color: #fff;
  margin: 0;
}

@media only screen and (max-width: 768px) {
  .portfolio-header h2 {
    font-size: 40px;
  }
  .project-description {
    width: 100%;
  }
  .project picture {
    width: 100%;
    height: auto;
    max-width: 100%;
    max-height: none;
    padding: 0;
  }
  .project-tags-container {
    margin: 0;
  }
}

#contact-page {
  width: 100vw;
  background: linear-gradient(
    112.14deg,
    rgba(128, 196, 205, 0.68) -50.2%,
    rgba(112, 129, 214, 0.477341) -11.67%,
    rgba(122, 220, 220, 0.53) 27.88%,
    rgba(226, 158, 250, 0.497914) 62.84%,
    rgba(252, 157, 214, 0.47) 79.49%
  );
  position: absolute;
  top: 0;
}

@media only screen and (max-width < 768px) {
  #contact-page {
    padding: 0 5vw;
    width: 100vw;
  }

  #contact {
    width: 100%;
  }
}

